// hide or show form
function atcHandleFormToggle() {
    const form = this.parentElement.querySelector('.atc-form');

    if (form.style.display === 'none') {
        form.style.display = '';
    } else {
        form.style.display = 'none';
    }
}

// toggle-on: disable multi date, toggle-off: revert multi date to initial state
function atcHandleRecurrenceToggle() {
    const toggleElements = document.querySelectorAll('fieldset[data-atc-toggled-by="' + this.name + '"] > .btn-group-yesno > input');

    toggleElements.forEach((el, key) => {
        const initialState = el.parentElement.parentElement.dataset.atcInitialState;

        // switch to "no" and trigger click event
        if (this.value === '1') {
            if (el.value === '0') {
                if (el.checked) return;

                // enable "no" & trigger event
                el.setAttribute('checked', 'checked');
                el.dispatchEvent(new MouseEvent('click'));
            } else {
                el.removeAttribute('checked');
            }
        }

        // switch back to initial state
        if (this.value === '0' && initialState === '1') {
            if (el.value === '0' && el.checked) {
                el.removeAttribute('checked');
            } else {
                // enable "yes" & trigger event
                el.setAttribute('checked', 'checked');
                el.dispatchEvent(new MouseEvent('click'));
            }
        }
    });
}

// hide or show html for linked start/end date & time
function atcHandleMultiDatesToggle() {
    const toggleElements = document.querySelectorAll('div[data-atc-toggled-by="' + this.name + '"]');

    toggleElements.forEach(el => {
        // show
        if (this.value === '0') {
            el.style.display = '';
        }

        // hide
        if (this.value === '1') {
            el.style.display = 'none';
        }
    })
}

window.addEventListener('load', () => {
    const formToggleBtns = document.querySelectorAll('.js-atc-form-toggle');
    const recurrenceToggleInput = document.querySelectorAll('.js-atc-recurrence-toggle>input');
    const multiDatesToggleInputs = document.querySelectorAll('.js-atc-multi-dates-toggle>input');

    formToggleBtns.forEach(btn => {
        btn.addEventListener('click', atcHandleFormToggle)
    });

    recurrenceToggleInput.forEach(input => {
        input.addEventListener('click', atcHandleRecurrenceToggle);
    });

    multiDatesToggleInputs.forEach(input => {
        input.addEventListener('click', atcHandleMultiDatesToggle)
    });
});
